<template>
  <section id="introduction">
    <div class="wave-section3-content">
      <img class="logo" src="../assets/about.png" alt="selfpick" />
      <div class="content">
        <img src="../assets/s3-jack.png" />
        <div>
          <p>SELFPICK 是一間以「沈浸式娛樂」為命題的新創新媒體公司，「沈浸式娛樂」從虛實整合做起，
          延伸到「視覺」「聽覺」「嗅覺」「味覺」的SELFPICK專屬五感體驗，開創其周邊商品以及概念，
          最終匯聚成一個具有品牌價值的意念「做自己的選擇，走對的路」。</p>
        </div>
      </div>
    </div>
    <div class="wave-section3 wave-section3-1" />
    <div class="wave-section3 wave-section3-2" />
    <div class="wave-section3 wave-section3-3" />
    <div class="wave-section3 wave-section3-4" />
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Introduction',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('#introduction .content', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  #introduction {
    position: relative;
  }

  .wave-section3-content {
    height: 100vh;
    max-height: 750px;
    position: relative;
    z-index: 5;

    @media screen and (min-width: 768px){
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;
    }

    .content {
      position: relative;
      padding: 25% 0;
      z-index: 2;

      @media screen and (min-width: 768px){
        max-width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
      }

      img {
        max-width: 60%;

        @media screen and (min-width: 768px){
          max-width: 35%;
          margin-left: 10%;
        }
      }

      > div {
        max-width: 90%;
        margin: auto;

        @media screen and (min-width: 768px){
          max-width: 55%;
          margin-left: 10%;
        }
      }

      p {
        color: #fff;
        font-size: 18px;
        text-align: left;
        line-height: 2em;
      }
    }
  }

  .wave-section3 {
    width: 100%;
    height: 100vh;
    max-height: 750px;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate3d(0, 0, 0);
  }

  .wave-section3-1 {
    background: url('../assets/s3-01.png') repeat-x;
    background-size: 1440px 100%;
    background-position: center;
    z-index: 4;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section3-2 {
    background: url('../assets/s3-02.png') repeat-x;
    background-size: 1440px 100%;
    background-position: center;
    z-index: 3;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section3-3 {
    background: url('../assets/s3-03.png') repeat-x;
    background-size: 1440px 100%;
    background-position: center;
    z-index: 2;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section3-4 {
    background: url('../assets/s3-04.png') repeat-x;
    background-size: 1440px 100%;
    background-position: center;
    z-index: 1;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }
</style>



