<template>
  <section id="contact">
    <div class="wave-section5-content">
      <img class="logo" src="../assets/s5-contact.png" alt="selfpick" />
      <div class="content icons">
        <a href="https://www.facebook.com/selfpick/" target="_blank">
          <img src="../assets/01-facebook.png" alt="selfpick.cc" />
          <img src="../assets/01-facebook-h.png" alt="selfpick.cc" />
          <span>SELFPICK</span>
        </a>
        <a href="https://www.instagram.com/selfpick/" target="_blank">
          <img src="../assets/02-ig.png" alt="selfpick.cc" />
          <img src="../assets/02-ig-h.png" alt="selfpick.cc" />
          <span>SELFPICK</span>
        </a>
        <a href="mailto:ourselves@selfpick.cc" target="_blank">
          <img src="../assets/03-mail.png" alt="selfpick.cc" />
          <img src="../assets/03-mail-h.png" alt="selfpick.cc" />
          <span>SELFPICK.CC</span>
        </a>
        <a href="https://selfpick.cc" target="_blank">
          <img src="../assets/04-s.png" alt="selfpick.cc" />
          <img src="../assets/04-s-h.png" alt="selfpick.cc" />
          <span>SELFTOKEN.CO</span>
        </a>
        <a href="https://www.facebook.com/selfpick/" target="_blank">
          <img src="../assets/05-doublesquare.png" alt="selfpick.cc" />
          <img src="../assets/05-doublesquare-h.png" alt="selfpick.cc" />
          <span>SELF BAR</span>
        </a>
      </div>
    </div>
    <div class="wave-section5 wave-section5-1" />
    <div class="wave-section5 wave-section5-2" />
    <div class="wave-section5 wave-section5-3" />
    <div class="wave-section5 wave-section5-4" />
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Work',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.wave-section5-content', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  #contact {
    position: relative;
  }

  .wave-section5-content {
    height: 100vh;
    max-height: 750px;

    position: relative;
    z-index: 5;

    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;
    }

    .content {
      max-width: 80%;
      width: 100%;
      position: relative;
      margin: auto;
      padding-top: 30%;
      z-index: 2;

      @media screen and (min-width: 768px) {
        padding-top: 0%;
        display: flex;
        justify-content: space-around;
      }

      > a {
        width: 45%;
        height: auto;
        margin: 0 10px;
        padding: 20px;
        border-radius: calc(40vw / 4);
        display: inline-block;
        text-align: center;
        background-color: rgba(227, 175, 167, 0.6);
        text-decoration: unset;
        font-size: 0.8em;

        @media screen and (max-width: 768px) {
          margin: 0 5px 30px;

          &:nth-child(1) {
            margin: 10% auto;
            display: block;
          }
        }

        > * {
          margin: auto;
          display: block;
        }

        @media screen and (min-width: 768px) {
          width: 180px;
          height: 180px;
          border-radius: 60px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          font-size: 1em;
        }

        img {
          // width: 50%;
          // height: 50%;

          @media screen and (min-width: 768px) {
            width: auto;
          }

          &:nth-child(2) {
            display: none;
          }
        }

        &:hover {
          img {
            &:nth-child(1) {
              display: none;
            }

            &:nth-child(2) {
              display: block;
            }
          }
        }
      }

      span {
        margin-top: 10px;
        color: #ffffff;
      }
    }
  }

  .wave-section5 {
    width: 100%;
    height: 100vh;
    max-height: 750px;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate3d(0, 0, 0);
  }

  .wave-section5-1 {
    background: url('../assets/s5-01.png') repeat-x;
    background-size: 100% 100%;
    background-size: 1440px 100%;
    z-index: 4;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section5-2 {
    background: url('../assets/s5-02.png') repeat-x;
    background-size: 100% 100%;
    background-size: 1440px 100%;
    z-index: 3;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section5-3 {
    background: url('../assets/s5-03.png') repeat-x;
    background-size: 100% 100%;
    background-size: 1440px 100%;
    z-index: 2;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }

  .wave-section5-4 {
    background: url('../assets/s5-04.png') repeat-x;
    background-size: 100% 100%;
    background-size: 1440px 100%;
    z-index: 1;

    @media screen and (min-width: 768px){
      background-size: 100% 100%;
    }
  }
</style>



