<template>
  <section id="landing">
    <div class="wave-content">
      <h2>{{ title }}</h2>
      <img class="logo" src="../assets/sp-logo.png" alt="selfpick" />
    </div>
    <div class="wave wave-1" />
    <div class="wave wave-2" />
    <div class="wave wave-3" />
    <div class="wave wave-4" />
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Landing',
  data() {
    return {
      msg: 'section 1 Ya',
      title: 'We pick our own path',
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.wave-content h2', {
      origin: 'left',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  @keyframes waving {
    0% {
      transform: translate3d(0, 0, 0);
    }
    100% {
      transform: translate3d(-100vw, 0, 0);
    }
  }

  section {
    position: relative;
    color: #fff;
    overflow: hidden;
  }

  .wave-content {
    height: 100vh;
    max-height: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 5;

    h2 {
      position: relative;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 3px;
      text-transform: uppercase;
      z-index: 2;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 5%;
      z-index: 1;
    }
  }

  .wave {
    width: 400%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }

  .wave-1 {
    background: url('../assets/01.png') repeat-x;
    background-size: auto calc(145vh);
    z-index: 4;
    animation: waving 10s 0.5s linear infinite;

    @media screen and (min-width: 768px){
      background-size: auto;
    }
  }

  .wave-2 {
    background: url('../assets/02.png') repeat-x;
    background-size: auto calc(145vh);
    z-index: 3;
    animation: waving 10s 0.8s linear infinite;

    @media screen and (min-width: 768px){
      background-size: auto;
    }
  }

  .wave-3 {
    background: url('../assets/03.png') repeat-x;
    background-size: auto calc(145vh);
    z-index: 2;
    animation: waving 10s 1.1s linear infinite;

    @media screen and (min-width: 768px){
      background-size: auto;
    }
  }

  .wave-4 {
    background: url('../assets/04.png') repeat-x;
    background-size: auto calc(145vh);
    z-index: 1;
    animation: waving 10s 1.4s linear infinite;

    @media screen and (min-width: 768px){
      background-size: auto;
    }
  }
</style>



