<template>
  <section class="scroll-part">
    <div id="nav">
      <nav v-bind:class='{ "open-menu": isActive }'>
        <div class="nav-container">
          <div
            v-for="item in navItems"
            :key="item.id"
          >
            <a>{{ item.name }}</a>
          </div>
        </div>
      </nav>
      <button
        class="menu-links hamburger hamburger--collapse"
        v-bind:class='{ "is-active": isActive }'
        type="button"
        v-on:click="toggleMenu">
        <span class="hamburger-box">
          <span class="hamburger-inner"></span>
        </span>
      </button>
    </div>
    <div class="sidebar">
      <a class="sidebar-1" href="http://selfpick.cc/articles/" target="_blank">
        <img src="../assets/side-1.png" />
      </a>
      <a class="sidebar-2" href="#">
        <img src="../assets/side-2.png" />
      </a>
      <a class="sidebar-3" href="https://www.facebook.com/selfpick/" target="_blank">
        <img src="../assets/side-3.png" />
      </a>
      <a class="sidebar-4" href="https://www.instagram.com/selfpick/" target="_blank">
        <img src="../assets/side-4.png" />
      </a>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Menu',
  data() {
    return {
      navItems: [
        { id: 1, name: 'Member' },
        { id: 2, name: 'About' },
        { id: 3, name: 'Work' },
        { id: 4, name: 'Contact' },
      ],
      isActive: false,
    };
  },
  methods: {
    toggleMenu() {
      this.isActive = !this.isActive;
    },
  },
};
</script>

<style lang="scss" scoped>
  @import "../hamburgers.css";

  section {
    max-width: 1440px;
    position: fixed;
    z-index: 100;
  }

  nav {
    position: absolute;
    top: -60px;
    width: 100%;
    opacity: 0;
    background-color: rgb(110, 49, 31);
    z-index: 6;
    transition: all 0.5s;

    @media screen and (min-width: 768px) {
      background-color: #1B32BB;
    }

    &.open-menu {
      top: 0;
      opacity: 1;
    }

    .nav-container {
      // width: 40%;
      height: 100vh;
      margin: auto;
      padding: 30% 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      align-content: space-evenly;
      font-size: 20px;
      text-transform: uppercase;

      @media screen and (min-width: 768px) {
        width: 90%;
        height: 60px;
        padding: 0;
        flex-direction: row;
        align-items: center;
      }
    }

    div {
      flex: 1;
      display: flex;
      align-content: center;
      align-items: center;
      justify-content: center;

      a {
        color: #fff;
        font-size: 2em;
        cursor: pointer;

        @media screen and (min-width: 768px) {
          height: 60px;
          padding-top: 12px;
          font-size: 25px;
          flex-direction: row;
          align-items: center;
        }
      }
    }
  }

  .menu-links {
    position: absolute;
    // width: 30px;
    // height: 30px;
    top: 0;
    left: 0;
    cursor: pointer;
    z-index: 7;

    @media screen and (min-width: 768px) {
      right: 0;
      left: auto;
    }

    .menu-bar {
      display: block;
      width: 30px;
      height: 3px;
      background-color: #fff;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      margin: 0 auto 8px;
      transition: background-color 0.2s;
    }
  }

  .sidebar {
    width: 50%;
    position: absolute;
    top: 0;
    right: 5%;
    display: flex;
    justify-content: space-around;
    cursor: pointer;
    z-index: 6;

    @media screen and (min-width: 768px) {
      width: auto;
      top: auto;
      bottom: 20%;
      right: auto;
      left: 5%;
      display: block;
    }

    a {
      display: block;
      margin: 20px 0;
      flex-direction: column;
      cursor: pointer;
      z-index: 10;

      img {
        width: 25px;

        @media screen and (min-width: 768px) {
          width: auto;
        }
      }
    }
  }
</style>



